import React, { useEffect, useState } from "react";
import { Anchor } from "grommet";
import Parse, { searchObject, updateObject, createObject } from "../library/parse";
import CRUDForm from '../containers/CRUDForm'
const Component = () => {
  const [rolePublicProfile, setRolePublicProfile] = useState({});

  useEffect(() => {
    searchObject("RolePublicProfile", {
      limit: 1,
      role: {
        f: 'equalTo',
        t: 'pointer',
        v: `User-${Parse.User.current().id}`,
        c: 'RolePublicProfile'
      }
    }).then((objects) => objects[0] ? setRolePublicProfile(objects[0]) : setRolePublicProfile({}));
  }, []);

  return (
    <>
        <CRUDForm
            mode={rolePublicProfile.id ? 'update' : 'create' }
            onClick={(action, model) => {
              if(action === 'create') {
                createObject('RolePublicProfile', model)
              }

              if(action === 'update') {
                updateObject('RolePublicProfile', model)
              }
            }}
            model={rolePublicProfile}
            formConfig={{
              config: [{
                property: "name",
                headerText: "Name",
              }],
            }}
        />
    </>
  );
};

export default Component;
